
import React from "react";

import "./About.css";
import Intro from "../Intro/Intro";
import * as app_constants from '../../constants/program_constants';

const About = props => {
    return (
        <div className="about">    
        <Intro heading={app_constants.app_name} slogan={app_constants.app_descrition}/>
            <div className="row">
                                    
                        <div className="col-lg-3">

                            <div className="box box-primary">
                                <div className="box box-header">
                                    <h3 className="box-title"> <strong><i className="fa fa-info"> </i> About </strong></h3>
                                </div>
                            
                                <ol className="list-group">

                                    <li className="list-group-item"><button className="btn btn-app btn-outline-dark btn-box-tool btn-block"><strong> <i className="fa fa-info-circle"> </i> About Site </strong> </button> </li>
                                    <li className="list-group-item"><button className="btn btn-app btn-outline-dark btn-box-tool btn-block"><strong> <i className="fa fa-info-circle"> </i> Cathida </strong> </button> </li>
                                </ol>


                            </div>

                        </div>
                        <div className="col-lg-9">
                            < div className="box box-primary">
                                <div className="box box-header">
                                    <h3 className="box-title"> <strong> <i className="fa fa-info-circle"> </i> About </strong></h3>
                                </div>


                            </div>


                        </div>
                    
            </div>            
        </div>
    )
}


export default About