import React, {Component}  from 'react';
import './Home.css';
import Intro from '../Intro/Intro';
import * as app_constants from '../../constants/program_constants';

import SignUpPage from '../User/Signup/Signup';
import {auth,
    firebase} from '../../firebase/firebase';
import SignInForm  from '../User/SignIn/SignIn';

class HomePage extends Component{
    constructor(){
        super();
        this.state = {
            userid : ""
        };

    };

    render(){
        return(
            <div className="home">
                <div className="box box-header">
                    <h3 className="box-title"> <strong> <i className="fa fa-home"> </i> Cathida</strong></h3>
                </div>


            </div>
        )
    }
}


class Home extends Component{
    constructor (){
        super();
        this.state = {

            user_registered : false,
            user_logged_in : false,

            load_offers : false,
            load_register : false,
            load_home : true,
            load_signin: false,
        };

        this.onSwitch = this.onSwitch.bind(this);
        this.onLogin = this.onLogin.bind(this);


    }

    onSwitch(e){
        switch (e.target.name) {
            case "home" :
                this.setState({
                    load_home : true,
                    load_register : false,
                    load_signin: false,

                });break;
            case "register":
                this.setState({
                    load_home : false,
                    load_register : true,
                    load_signin: false,

                });break;
            case "login" :
                this.setState({
                    load_home : false,
                    load_register : false,
                    load_signin: true,

                });break;
            default:
                this.setState({
                    load_home : true,
                    load_register : false,
                    load_signin: false,

                });break;
        }
    }

    onLogin(e){

    }

    onSignOut(e){
        auth.signOut();
    }

    componentWillMount(){
        this.removeAuthListener = auth.onAuthStateChanged((user) => {
              if (user) {
                this.setState({
                    user_registered : true,
                    user_logged_in : true,
                })
              } else {
                this.setState({
                    user_registered : false,
                    user_logged_in : false,
                })
              }
            })

    }

 componentWillUnmount() {
     this.removeAuthListener();
 }


    render() {
        return (
            <div className="home">
                <Intro heading={app_constants.app_name} slogan={app_constants.app_descrition}/>

                <div className="row">
                    <div className="col-lg-3">
                        <div className="box box-primary">
                            <div className="box box-header">
                                <h3 className="box-title"><i className="fa fa-home"> </i> Home </h3>
                            </div>

                            <div className="box box-footer">
                                <button className="btn btn-app btn-outline-dark btn-box-tool btn-block" name="home" onClick={e => this.onSwitch(e)}><strong> <i
                                    className="fa fa-home"> </i> Home </strong></button>


                                {
                                    (!this.state.user_logged_in) ?
                                        <button className="btn btn-app btn-outline-dark btn-box-tool btn-block" name="login" onClick={e => this.onSwitch(e)}><strong> <i
                                            className="fa fa-sign-in"> </i> Login </strong></button>
                                    :
                                    <button className="btn btn-app btn-outline-dark btn-block" onClick={ e => this.onSignOut(e)}>
                                        <strong><i className="fa fa-sign-out"> </i> Sign Out </strong></button>

                                }
                                {
                                    (!this.state.user_registered)?
                                        <button className="btn btn-app btn-outline-dark btn-box-tool btn-block" name="register" onClick={e => this.onSwitch(e)}><strong> <i
                                            className="fa fa-sign-in"> </i> Register </strong></button>
                                    :""
                                }

                            </div>

                        </div>

                    </div>
                    <div className="col-lg-9">
                        <div className="box box-body">
                            {
                                (this.state.load_home) ?
                                    <HomePage />
                                :""
                            }
                            {
                                (this.state.load_register) ?
                                    <SignUpPage/>

                                : ""
                            }
                            {
                                (this.state.load_signin) ?
                                    <SignInForm
                                    />
                                :
                                    ""
                            }
                        </div>

                    </div>
                </div>


            </div>

        )
    }
    
}

export default Home;
