
import React from "react";
import "./ContactMenu.css";

const ContactMenu = props => {
    return (
        <div className="contact-menu">
            <div className="box box-primary">
                <div className="box box-header">
                    <h3 className="box box-title"> <i className="fa fa-envelope"> </i> Contact Menu</h3>
                </div>                
                <ul className="list-group">
                    <li className="list-group-item"><button className="btn btn-app btn-outline-dark btn-block" onClick={e => props.onShowContactDetails(e)}> <strong> <i className="fa fa-info-circle"> </i> Contact Details</strong></button></li>
                    <li className="list-group-item"><button className="btn btn-app btn-outline-dark btn-block" onClick={e => props.onShowContactForm(e)}> <strong> <i className="fa fa-envelope"> </i> Contact Form</strong></button></li>
                </ul>
            </div>
        </div>
    )
}

export default ContactMenu