import React, { Component } from "react";

import "./Admin.css";
import Intro from "../Intro/Intro";
import User from "../User/User";
import Axios from "axios";
import * as app_constants from '../../constants/program_constants';


import SignOutButton from "../User/SignOut/SignOut";

import {
    firebase, auth
} from '../../firebase'; 
import SignInPage from '../User/SignIn/SignIn';
class Admin extends Component{
    constructor(){
        super();
        this.state = {
            isUserLoggedIn : true,
        };

        this.onShow = this.onShow.bind(this);
    }

    onShow(e){
        
        switch(e.target.name){

            case 'myprofile': this.setState({
                load_my_profile: true,
                load_public_profiles: false,
                load_friends_profiles: false,
                load_messages: false,
                load_wallet: false,
                load_signout: false,
                load_p2ploans : false

            }); break;

            case 'publicprofiles': this.setState({
                load_my_profile: false,
                load_public_profiles: true,
                load_friends_profiles: false,
                load_messages: false,
                load_wallet: false,
                load_signout: false,
                load_p2ploans : false
            });break;

            case 'friendsprofiles': this.setState({
                load_my_profile: false,
                load_public_profiles: false,
                load_friends_profiles: true,
                load_messages: false,
                load_wallet: false,
                load_signout: false,
                load_p2ploans : false
            });break;
            case 'messages': this.setState({
                load_my_profile: false,
                load_public_profiles: false,
                load_friends_profiles: false,
                load_messages: true,
                load_wallet: false,
                load_signout: false,
                load_p2ploans : false
            }); break;
            case 'wallet': this.setState({
                load_my_profile: false,
                load_public_profiles: false,
                load_friends_profiles: false,
                load_messages: false,
                load_wallet: true,
                load_signout: false,
                load_p2ploans : false
            }); break;

            case "signout" : this.setState(
                {
                    load_my_profile: false,
                    load_public_profiles: false,
                    load_friends_profiles: false,
                    load_messages: false,
                    load_wallet: false,
                    load_signout: true,
                    load_p2ploans : false
                }
            );break;

            case "p2ploans" : this.setState(
                {
                    load_my_profile: false,
                    load_public_profiles: false,
                    load_friends_profiles: false,
                    load_messages: false,
                    load_wallet: false,
                    load_signout: false,
                    load_p2ploans : true

                }
            );break;


            default:break;
        }
    }
    render(){

        let isUserLoggedIn = ((firebase.auth.currentUser !== "") && (firebase.auth.currentUser  !== null ));

        if (isUserLoggedIn){
                return(
                        <div className="admin">
                            <div className="box box-body">
                                <Intro heading={app_constants.app_name} slogan={app_constants.app_descrition} />
                                <div className="box box-header">
                                    <h3 className="box-title">  <i className="fa fa-dashboard"> </i> Admin </h3>
                                    <button type="button" className="btn btn-warning pull-right" name="signout" onClick={e => this.onShow(e)}><strong> <em> <i className="fa fa-sign-out"> </i> SignOut</em></strong></button>
                                </div>
                                <div className="row">
                                    <div className="col-md-3">
                                        <div className="box box-primary">

                                        </div>
                                    
                                    </div>
                                    <div className="col-md-9">
                                    </div>
                                </div>


                            </div>
                        </div>
                    )
            }
            else{
            return (<SignInPage message={"Please sign in before using the administration module"} />)
            }
    }
}



export default Admin;