
import React from "react";

import "./Intro.css";


const Intro = props => {
    return (
        <div>
            <div className="box box-header">
                
                <h1 className="box-title"> <i className="fa fa-gg-circle"> </i>
                    { props.heading } </h1>
                <blockquote>
                    <small><strong> <i className="fa fa-gg-circle"> </i> {props.slogan}</strong></small>
                </blockquote>
            </div>

        </div>
    )
};

export default Intro;